import * as React from "react";
import { useState } from "react";

import { Grid, TextField, Button } from "@mui/material";

export default function CreateNewTodo(props) {
  const [todoValue, setTodoValue] = useState("");
  const handleChange = (evnt) => {
    setTodoValue(evnt.target.value);
  };

  const handleSubmit = (evnt) => {
    evnt.preventDefault();
    props.addTodo(props.id, todoValue);
    setTodoValue("");
  };
  return (
    <div>
      <Grid
        marginTop="30px"
        container
        spacing={0}
        direction="column"
        alignItems="center"
        // justifyContent="center"
        // style={{ minHeight: "100vh" }}
      >
        <form>
          <TextField
            onChange={handleChange}
            value={todoValue}
            id="standard-basic"
            label="Create a to-do"
            color="secondary"
          />
        </form>
        <Button sx={{ marginTop: 1 }} onClick={handleSubmit} variant="outlined">
          Submit
        </Button>
      </Grid>
    </div>
  );
}
