import React, { Component } from "react";
import { Grid, Box, Container, Input, Switch } from "@mui/material";
import DeleteIcon from "@mui/icons-material/Delete";
import ArrowForwardIcon from "@mui/icons-material/ArrowForward";
import EditIcon from "@mui/icons-material/Edit";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";

class Todo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      trashIsWarning: false,
      isEditing: false,
      todoValue: this.props.task,
      backButtonLit: false,
    };
    this.handleDelete = this.handleDelete.bind(this);
    this.shadeRed = this.shadeRed.bind(this);
    this.shadeBlue = this.shadeBlue.bind(this);
    this.toggleEditMode = this.toggleEditMode.bind(this);
    this.handleSubmitChange = this.handleSubmitChange.bind(this);
    this.handleChange = this.handleChange.bind(this);
    this.shadeGreen = this.shadeGreen.bind(this);
    this.shadeNotGreen = this.shadeNotGreen.bind(this);
    this.handleSwitch = this.handleSwitch.bind(this);
  }
  handleSwitch() {
    this.props.toggleCompleted(this.props.id, this.props.completed);
  }
  shadeNotGreen() {
    this.setState({ backButtonLit: false });
  }
  shadeGreen() {
    this.setState({ backButtonLit: true });
  }
  handleChange(evnt) {
    this.setState((st) => ({
      todoValue: evnt.target.value,
    }));
  }
  handleSubmitChange(event) {
    event.preventDefault();
    this.props.alterTodo(this.props.id, this.state.todoValue);
    this.setState({ isEditing: false });
  }
  toggleEditMode() {
    this.setState((st) => ({
      isEditing: !st.isEditing,
    }));
  }
  shadeBlue() {
    this.setState({ trashIsWarning: false });
  }
  shadeRed() {
    this.setState({ trashIsWarning: true });
  }
  handleDelete() {
    this.props.delete(this.props.id);
  }
  render() {
    return (
      <Grid
        marginTop="18px"
        marginBottom="11px"
        container
        justifyContent="flex-end"
        spacing={0}
        direction="column"
        alignItems="center"

        // justifyContent="center"
        // style={{ minHeight: "100vh" }}
      >
        <Box
          sx={{
            display: "flex",
            flexDirection: { xs: "column", md: "row" },
            alignItems: "center",
          }}
        >
          <Box
            sx={{
              display: "flex",
              justifyContent: "space-between",

              width: {
                md: 300, // theme.breakpoints.up('md')
                lg: 400, // theme.breakpoints.up('lg')
                xl: 500, // theme.breakpoints.up('xl')
              },
              pt: 1,
              pb: 1,
              pl: 1,
              pr: 1,
              border: 0.5,
              borderRadius: 3,
              borderColor: "blueviolet",
            }}
          >
            <Container
              sx={{
                display: "flex",
                justifyContent: "space-between",
              }}
            >
              {!this.state.isEditing ? (
                <Grid
                  sx={
                    ({ paddingTop: 0.5 },
                    this.props.completed
                      ? { textDecoration: "line-through" }
                      : "")
                  }
                >
                  {this.props.task}
                </Grid>
              ) : (
                <form>
                  <Input
                    value={this.state.todoValue}
                    name="change"
                    onChange={this.handleChange}
                    sx={{ paddingTop: 0.5 }}
                  ></Input>
                </form>
              )}

              {!this.state.isEditing ? (
                <Grid
                  sx={{
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                  }}
                  hover
                >
                  <DeleteIcon
                    color={this.state.trashIsWarning ? "error" : "secondary"}
                    onMouseEnter={this.shadeRed}
                    onMouseLeave={this.shadeBlue}
                    onClick={this.handleDelete}
                  />
                  <EditIcon
                    sx={{ paddingBottom: 0.25, paddingLeft: 1.5 }}
                    fontSize="inherit"
                    color="primary"
                    onClick={this.toggleEditMode}
                  />
                  <Switch
                    size="small"
                    checked={this.props.completed}
                    onChange={this.handleSwitch}
                    inputProps={{ "aria-label": "controlled" }}
                  />
                </Grid>
              ) : (
                <Grid>
                  <Grid hover>
                    <ArrowBackIcon
                      color={this.state.backButtonLit ? "success" : ""}
                      onMouseEnter={this.shadeGreen}
                      onMouseLeave={this.shadeNotGreen}
                      onClick={this.toggleEditMode}
                    />
                  </Grid>
                  <Grid hover>
                    <ArrowForwardIcon
                      sx={{ paddingTop: 1.4 }}
                      color={this.state.trashIsWarning ? "error" : "seagreen"}
                      onMouseEnter={this.shadeRed}
                      onMouseLeave={this.shadeBlue}
                      onClick={this.handleSubmitChange}
                    />
                  </Grid>
                </Grid>
              )}
            </Container>
          </Box>
        </Box>
      </Grid>
    );
  }
}
export default Todo;
