import React from "react";
import Todo from "./Todo";
import CreateNewTodo from "./CreateNewTodo";
import { v4 as uuid } from "uuid";

import { Grid, Typography, Toolbar, Container } from "@mui/material";
class TodoList extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      todos: JSON.parse(window.localStorage.getItem("todos") || "[]"),
    };
    this.addTodo = this.addTodo.bind(this);
    this.deleteThisTodo = this.deleteThisTodo.bind(this);
    this.alterTodo = this.alterTodo.bind(this);
    this.toggleCompleted = this.toggleCompleted.bind(this);
  }
  toggleCompleted(id, isCompleted) {
    this.setState((currState) => ({
      todos: currState.todos.map((todo) => {
        if (todo.id === id) {
          return { ...todo, completed: !isCompleted };
        }
        return todo;
      }),
    }));
  }
  alterTodo(id, newContent) {
    console.log(id, newContent);
    this.setState((currState) => ({
      todos: this.state.todos.map((t) => {
        if (t.id === id) {
          return { ...t, task: newContent };
        }
        return t;
      }),
    }));
  }
  deleteThisTodo(id) {
    this.setState((currState) => ({
      todos: currState.todos.filter((todo) => todo.id !== id),
    }));
  }
  addTodo(id, todoContent) {
    if (!todoContent) {
      alert("you must have something in your todo in order to submit");
    } else {
      const newTodo = { task: todoContent, id: uuid(), completed: false };
      this.setState((currState) => ({
        todos: [...currState.todos, newTodo],
      }));
    }
  }
  componentDidUpdate() {
    window.localStorage.setItem("todos", JSON.stringify(this.state.todos));
  }
  componentDidMount() {
    console.log("mounted the component");
  }
  render() {
    let mappedTodos = this.state.todos.map((todo) => (
      <Todo
        delete={this.deleteThisTodo}
        key={todo.id}
        id={todo.id}
        task={todo.task}
        completed={todo.completed}
        toggleCompleted={this.toggleCompleted}
        alterTodo={this.alterTodo}
      />
    ));
    return (
      <div>
        <Grid container spacing={2}>
          <Grid item xs={12} md={12}>
            <CreateNewTodo addTodo={this.addTodo} />
            <Container>{mappedTodos}</Container>
          </Grid>
        </Grid>
      </div>
    );
  }
}
export default TodoList;
